import logo from './logo.svg';
import './App.css';
import React, { useState, useRef } from 'react';
import ReactQuill from 'react-quill';
import pdfMake from 'pdfmake';
import 'react-quill/dist/quill.snow.css';
import { Button } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';

import { saveAs } from 'file-saver';
import { pdfExporter } from 'quill-to-pdf';

import { QuillDeltaToHtmlConverter } from 'quill-delta-to-html'; 

import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";


function App() {
  let quillInstance: any;
  const editorRef = useRef(null);

  const [value, setValue] = useState('');
  var Size = ReactQuill.Quill.import('attributors/style/size');
  Size.whitelist = ['5px','6px', '7px', '8px','9px','10px', '16px', '14px','18px'];
  ReactQuill.Quill.register(Size, true);


var SizeStyle = ReactQuill.Quill.import('attributors/style/size');
ReactQuill.Quill.register(SizeStyle, true);

  async function showModal() {
    // @ts-ignore
    const delta = editorRef.current?.editor?.getContents();
    console.log(delta);
    let cfg = {};
    var converter = new QuillDeltaToHtmlConverter(delta.ops, cfg);
    var html = converter.convert(); 
    console.log(html);
    let pdf = new jsPDF('p', 'pt', 'a4');
    pdf.html(html, {
      callback: function () {
           pdf.save('web.pdf');
          //window.open(pdf.output('bloburl')); // use this to debug
      },
      margin: 20,
      autoPaging: 'text',
      width: 550, // <- here
      windowWidth: 1200 // <- here
  });
    // const pdfAsBlob = await pdfExporter.generatePdf(delta); // converts to PDF
    // saveAs(pdfAsBlob, "pdf-export.pdf"); 
    
  };

  return (
    <div className="App">
      <ReactQuill 
      theme="snow" 
      value={value} 
      onChange={setValue} 
      modules={{
        toolbar: [
          ["bold", "italic", "underline", "strike"],
          [{ color: [] }],
          [{ align: [] }],
          [{ list: "ordered" }, { list: "bullet" }],
          [{ header: [1, 2, 3, 4, 5, 6] }],
          [{size: ['5px','6px','7px','8px','9px','10px']}],
          ["blockquote","link"]
        ]
      }}
      ref={editorRef}/>
      {value}
      <Button type="primary" icon={<DownloadOutlined />} size={'large'} onClick={showModal}>
            Download
        </Button>
    </div>

  );
}

export default App;
